.container__pictures {
	display: flex;
	position: relative;
	z-index: 1;
	min-height: 22.5rem;
	width: 100%;
}

.container__pictures span {
	border-radius: 50%;
	position: absolute;
	background-size: 0.5rem;
	background-color: white;
	padding: 1rem;
	z-index: 2;
	display: block;
}
.container__pictures span:nth-child(2) {
	background-image: url("../../../assets/images/icon-previous.svg");
	left: 1rem;
	top: 50%;
}
.container__pictures span:last-child {
	background-image: url("../../../assets/images/icon-next.svg");
	right: 1rem;
	top: 50%;
}

.container__pictures span:hover {
	background-color: rgba(173, 173, 173, 0.548);
}
