.header {
	padding: 1.8rem;
	display: flex;
	align-items: center;
}

.icon-menu {
	height: 1.1rem;
	width: 1.1rem;
	position: relative;
	z-index: 16;
}
.open {
	background-image: url("../../assets/images/icon-menu.svg");
}

.close {
	background-image: url("../../assets/images/icon-close.svg");
}

.visible {
	visibility: visible;
	opacity: 1;
	transition: 0.3s ease-out;
}

.hidden {
	transition: 0.3s ease-out;
	visibility: hidden;
	transform: translateX(-2rem);
	opacity: 0;
}

.header nav {
	position: fixed;
	background-color: white;
	height: 100vh;
	width: 64%;
	top: 0;
	left: 0;
	z-index: 15;
}

.header nav ul {
	padding: 4rem 0 0 2rem;
	display: flex;
	flex-direction: column;
	gap: 2rem;
	text-align: left;
}

.logo {
	margin-left: 1rem;
}

.overlay {
	position: fixed;
	inset: 0 0 0 0;
	z-index: 10;
	background-color: rgba(49, 49, 49, 0.75);
}
