.container__cart {
	background-color: white;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 3rem auto auto auto;
	text-align: left;
	border-radius: 10px;
	position: absolute;
	top: 6.5rem;
	left: 3vw;
	width: 94vw;
	min-height: 14rem;
	padding: 1rem 1.4rem;
	z-index: 8;
	overflow-x: hidden;
	box-shadow: 1px 3px 2px rgba(128, 128, 128, 0.5), -1px 0px 2px rgba(128, 128, 128, 0.5);
}

.container__cart hr {
	width: 120%;
	margin-left: -1rem;
	border: none;
	border-bottom: 1px solid rgba(128, 128, 128, 0.24);
}

.button {
	padding: 0.4rem;
	display: grid;
	place-items: center;
	height: 2.6rem;
}

.close {
	visibility: hidden;
	opacity: 0;
	transform: translateY(-2rem);
	transition: 0.2s ease-out;
}

.open {
	transition: 0.2s ease-out;
	visibility: visible;
	opacity: 1;
}
