.container__price {
	display: flex;
	justify-content: flex-start;
	flex-direction: row;
	align-items: baseline;
	text-align: left;
	width: 100%;
	padding-block: 1rem;
}

.container__price span:first-of-type {
	margin: 0 1rem 0 2rem;
}
.container__price span:last-of-type {
	margin: 0 1rem 0 auto;
}
