.container__cart {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	margin: 0 auto;
}

.container__cart > div {
	position: relative;
	background-color: rgba(194, 194, 194, 0.225);
	padding: 0.5rem;
	width: 90%;
	margin: 0 auto;
	border-radius: 4px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.container__cart > div > input {
	outline: none;
	border: none;
	background-color: transparent;
	display: grid;
	place-items: center;
	text-align: center;
}

.container__cart button {
	padding: 1rem;
	gap: 1rem;
}
